import interfaces from '../../utils/interfaces.js';
var common = require('../../utils/common.js');
import app from "../../App.vue";
export default {
	data() {
		return {
			data:[],
			user_info: '',
			sex:1,
			id:0
		}
	},
	onLoad(options) {
		this.id = options.id ? options.id : 0;
	},
	onShow() {
		this.user_info = app.globalData.user_info;
		if (this.user_info) {
			this.loadData();
		}else{
			wx.reLaunch({
				url: '/pages/auth/index',
			})
		}
	},
	methods: {
		//获取员工信息
		loadData() {
			this.request({
				url: interfaces.addressUserInfo,
				data:{
					id:this.id
				},
				header: {
					token: this.user_info.token
				},
				method: 'POST',
				success: res => {
					//console.log(res);
					if (res.code == 200) {
						this.data=res.data;
						this.sex=res.data.sex;
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
		},
		changeSex(sex){
			this.sex=sex;
		},
		//表单提交事件
		formSubmit: function(e) {
			var data = e.detail.value;
			data.sex=this.sex;
			data.id=this.id;
			if (data.linkman == '') {
				wx.showToast({
					title: '请输入联系人',
					icon: "none",
				});
				return;
			}
			if (data.job == '') {
				wx.showToast({
					title: '请输入职位',
					icon: "none",
				});
				return;
			}
			if (!common.checkMobile(data.mobile)) {
				wx.showToast({
					title: '手机号码格式有误',
					icon: "none",
				});
				return;
			}
			
			this.request({
				url: interfaces.addressEditUser,
				data: data,
				header: {
					token: this.user_info.token
				},
				method: 'POST',
				success: res => {
					console.log(res);
					if (res.code == 200) {
						wx.showToast({
							title: res.msg,
							icon: 'success'
						})
						setTimeout(function() {
							//返回地址列表
							wx.navigateBack({
								delta: 1
							})
						}, 1500);
					} else {
						app.isLogin(res.code, res.msg);
					}
				}
			});
			
		},
	}
}
